import { Component, OnInit, ElementRef, ViewChild, HostListener } from '@angular/core';

import Map from 'ol/Map';
import OSM from 'ol/source/OSM';
import OlTileLayer from 'ol/layer/Tile';
import OlView from 'ol/View';
import { fromLonLat } from 'ol/proj';
import  Vector  from 'ol/source/Vector';
import  VectorLayer  from 'ol/layer/Vector';
import { Circle } from 'ol/geom';
import  Feature  from 'ol/Feature';
import { Style,Stroke,Fill } from 'ol/style';

@Component({
  selector: 'app-play-memory',
  templateUrl: './play-memory.component.html',
  styleUrls: ['./play-memory.component.css']
})
export class PlayMemoryComponent implements OnInit {
  private map: Map;
  private layer: OlTileLayer;
  private view: OlView;
  private color : string = 'rgba(139,91,251,0.61)';
  private size : number = 1500;

  constructor() { }

  ngOnInit() {
    this.layer = new OlTileLayer({
      source: new OSM()
    });

    this.view = new OlView({
      center: fromLonLat([126.97796919000007, 37.566535]),  //default position
      zoom: 11
    });

    this.map = new Map({
      target: 'map',
      layers: [this.layer],
      view: this.view
    });

    this.map.on('click', (args)=>{
      this.clickEffect(args.coordinate[0],args.coordinate[1]);
    });    
  }

  private clickEffect(arg1, arg2){
    var vectorSource = new Vector({projection: 'EPSG:4326'});
    var circle = new Circle([arg1,arg2], this.size);
    var CircleFeature = new Feature(circle);

    vectorSource.addFeatures([CircleFeature]);

    var vectorLayer =new VectorLayer({
      source: vectorSource,
      style: [
      new Style({
          stroke: new Stroke({
              color: this.color,
              width: 2
          }),
          fill: new Fill({
              color: this.color
          })
      })]
    });
    this.map.addLayer(vectorLayer);
  }

  private chagneColor(){
    //console.log(this.color);
  }

  private ranger(val){
    this.size = Number(val);
    console.log(this.size, typeof this.size);
  }
}
