import { Component, OnInit } from '@angular/core';
import {DaoService} from '../service/dao.service';
import {Info} from '../service/app.info';
import { StyleClassService } from '../service/style-class.service';
import { Observable } from 'rxjs';
import { SessionService } from '../service/session.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {

  private dao : DaoService;
  private style : StyleClassService;
  private values : Info;
  private obj : Observable<any[]>;
  private len ='';

  constructor(serv : DaoService, serv2 : StyleClassService, private session : SessionService, private router : Router) { 
    this.dao = serv;
    this.style = serv2;
    this.values = {id:'', pwd:'', time:'', auth : ''};
  }

  ngOnInit() {

  }
  
  tryToAccess(){
    console.log(this.values);
    this.obj = this.dao.getItem('items',['id','pwd'],[this.values.id,this.values.pwd],['==','==']);
    this.obj.forEach( (val)=>{
      if(val.length > 0){
        let ok = {
          id:val[0].id,
          pwd:'OK',
          time: new Date().toString(),
          auth:val[0].auth
        } as Info;
        this.session.setInfor(ok);
        this.router.navigateByUrl('main');
      } 
    });
  }


}
