import { Injectable, Host, Optional } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Observable, BehaviorSubject } from 'rxjs';
import { map } from "rxjs/operators";
import { AngularFireStorage, AngularFireStorageReference, AngularFireUploadTask } from '@angular/fire/storage';


@Injectable({
  providedIn: 'root'
})
export class DaoService {
  //데이터 베이스 관련 객체
  public DB_NAME : string = 'items';
  private DataBase : AngularFirestore;
  private itemsCollection: AngularFirestoreCollection<any>;
  private items: Observable<any[]>;
  private items2 : BehaviorSubject<any[]> = new BehaviorSubject([]);

  //저장소 관련 객체
  private storage : AngularFireStorage;
  private ref: AngularFireStorageReference;
  private task: AngularFireUploadTask;  

  public status : string;

  //의존성 주입
  constructor(@Optional() @Optional() db : AngularFirestore,@Host() @Optional() strg : AngularFireStorage) { 
    this.DataBase = db;
    if(this.DataBase != null && this.DataBase != undefined){
      this.status = 'OK';
    }
    else{
      this.status = 'NO'
    }
    this.storage = strg;
  }

  getGenerateId(){
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz012345_6789";
    for (var i = 0; i <22 ; i++){
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }

  //데이터 가져오기, 단일조건
  getItem(db_name : string, param : Array<any>, value: Array<any>, operator : Array<any>){
    const itemCollec = this.DataBase.collection<any>(db_name, ref =>{
      let query : firebase.firestore.CollectionReference | firebase.firestore.Query = ref;
      if(param != null){
        for(var p =0 ; p < param.length; p++){
          query = query.where(param[p], operator[p], value[p]);
        }
      }
      return query;
    });
    const itm = itemCollec.valueChanges();
    return itm;
  }

  //Paging
  getItemPaging(db_name : string, param : any, value: any, operator : any,afterNum){
    if(param == null || param == ''){
      this.itemsCollection = this.DataBase.collection<any>(db_name, (ref) =>ref);
    } else{
      this.itemsCollection = this.DataBase.collection<any>(db_name,  ref =>
        ref.orderBy('date','desc').where(param,operator,value).limit(afterNum)
      );
    }
    this.itemsCollection.valueChanges().forEach((val)=>{
      this.items2.next(val);
    });
    this.items = this.items2.asObservable();
    return this.items;
  }

  //데이터 넣기
  setItem(db_name : string,item : any){
    this.DataBase.collection(db_name).valueChanges();    
    if(this.itemsCollection == undefined || this.itemsCollection == null){
      this.itemsCollection = this.DataBase.collection<any>(db_name);
      this.items = this.itemsCollection.valueChanges();
    }
    this.itemsCollection.add(item);
    this.items = this.itemsCollection.valueChanges();
  }

  private subscription : any;

  //데이터 수정/삭제
  alterItem(db_name : string, param : any, value: any, operator : any, changeItem : any, type: any){

    if(this.subscription != undefined && this.subscription != null){
      if(!this.subscription.closed){
        this.subscription.unsubscribe();  //무한 구독 방지
      }
    }
    
    var num = 0;
    this.subscription = this.itemsCollection.stateChanges().pipe(
      map(
          changes => { return changes.map(a => {
            const data = a.payload.doc.data() as any;
            const ID = a.payload.doc.id;
            var datas = changeItem;
            if(data.idx == changeItem.idx){
              if(type == null){
                this.itemsCollection.doc(ID).delete();
              } else if(type =='set'){
                this.itemsCollection.doc(ID).set(datas);
              } else{
                this.itemsCollection.doc(ID).update(datas);
              }
               num ++;
            }
            return data;
          });
        }
      )).subscribe();  
  }

  //파일 업로드
  fileUpload(filePath, file) : void{
    
    this.task = this.storage.upload(filePath, file);
    console.log(this.task);
  }

  //파일 주소 가져오기
  getFileAddress(){
    return this.storage;
  }
 
  //파일 삭제
  deleteFile(path : string){
    const refer = this.storage.ref(path);
    refer.delete();
  }

}
