import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { StyleClassService } from '../service/style-class.service';
import { SessionService } from '../service/session.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @Input() activeType;
  @Output() sendToTarget = new EventEmitter<any>();

  private whoIsActive = {
    first : false,
    second : false,
    third : false,
    fourth : false,
    fifth : false
  }

  private INF : any;
  
  
  constructor(private styles : StyleClassService,private session : SessionService, private router : Router) { 
    this.INF = session.getInfor();
  }

  ngOnInit() {
    this.changeHeaderColor();
  }

  changeHeaderColor(){
    this.whoIsActive.first = false;
    this.whoIsActive.second = false;
    this.whoIsActive.third = false;
    this.whoIsActive.fourth = false;
    switch (this.activeType) {
      case 'first':
        this.whoIsActive.first = true;
        break;
      case 'second':
        this.whoIsActive.second = true;
        break;
      case 'third':
        this.whoIsActive.third = true;
        break;
      default:
        this.whoIsActive.fourth = true;
    } 
  }

  ngMyStyle(myBoolean){ 
     if(myBoolean){
	      return this.styles.header_style_active;
     }
     return this.styles.header_style_sleep;
  }
  gotoPage(type){
     this.sendToTarget.emit(type);
     if(type == 1 || type == 2){
      this.activeType = 'first';
    } else if(type == 3 || type == 4){
      this.activeType = 'second';
    } else if(type == 5 ){
      this.activeType = 'third';
    } else{
      this.activeType = 'fourth';
    }     
    this.changeHeaderColor();
  }

  logOut(){
    if(confirm('To do Log out?')){
      if(this.session.remove()){
        this.router.navigateByUrl('welcome');
      }
    }
  }
}
