import { Component, OnInit } from '@angular/core';
import { UploadEvent, UploadFile, FileSystemFileEntry } from 'ngx-file-drop';
import { DaoService } from 'src/app/service/dao.service';
import { Observable } from 'rxjs';
import { SessionService } from 'src/app/service/session.service';



@Component({
  selector: 'app-file-board',
  templateUrl: './file-board.component.html',
  styleUrls: ['./file-board.component.css']
})

export class FileBoardComponent implements OnInit {
  
  private list : Observable<any>;
  private tableName : string = 'files';
  private PATH : string = '/default/';
  private AUTH :string;

  constructor(private dao : DaoService, private session : SessionService) { 
    this.AUTH = session.getInfor().auth;
  }

  ngOnInit() {
    this.list = this.dao.getItemPaging(this.tableName,'','','',10);
  }

  public files: UploadFile[] = [];
 
  public dropped(event: UploadEvent) {
    for (const droppedFile of event.files) {
      if (droppedFile.fileEntry.isFile) {
        event.files.forEach((fileData)=>{
          this.files.push(fileData);
        });
      } else {
       // const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
       // console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }
 
  public fileOver(event){
    //console.log(event);
  }
 
  public fileLeave(event){
    //console.log(event);
  }

  delete(item, index){
    console.log(item);
    this.files = this.files.filter((res,idx)=>{
      if(index == idx){
        return false;
      } else{
        return true;
      }
    });
  }

  upload(){
    this.files.forEach((file)=>{
      var rename = this.dao.getGenerateId();
      var origin_name = file.fileEntry.name;
      var tmpSp = origin_name.split('.');
      var ext = tmpSp[tmpSp.length-1];
      rename = rename + '.' + ext;
      var data = {idx : rename, origin_name : origin_name, re_name : rename,reg_date : new Date()}
      console.log(rename);

      const fileEntry = file.fileEntry as FileSystemFileEntry;
      fileEntry.file((savedFile: File) => {
        this.dao.fileUpload(this.PATH + rename,savedFile);
        this.dao.setItem(this.tableName,data);
      });
    });
  }
  

  download(name : string){
    var obj = this.dao.getFileAddress();
    const refer = obj.ref(this.PATH + name);  
    refer.getDownloadURL().subscribe(url=>{
      //console.log('url -> ', url);
      var xhr = new XMLHttpRequest();
      xhr.responseType = 'blob';
      xhr.onload = function(event) {
        var blob = xhr.response;
        console.log(blob);
        var a = document.createElement('a');
        a.href = window.URL.createObjectURL(blob);
        a.download = name;
        a.dispatchEvent(new MouseEvent('click'));
      };
      xhr.open('GET', url, true);
      xhr.send();
    });
  }

  deleteItem(event, idx){
    console.log(idx);
    event.stopPropagation();
    if(!confirm('Do you want Delete the Data? ')){
      return;
    }    
    this.dao.alterItem(this.tableName, 'idx', idx, '==', {idx:idx}, null);    
    this.dao.deleteFile(this.PATH+idx);
  }



}
