import { Component, OnInit } from '@angular/core';
import { DaoService } from '../../service/dao.service';
import { SessionService } from '../../service/session.service';
import { Observable } from 'rxjs';
import { FormGroup, FormControl} from '@angular/forms';
import { Validators } from '@angular/forms';
import { filter } from 'rxjs/operators';


@Component({
  selector: 'app-simple-board',
  templateUrl: './simple-board.component.html',
  styleUrls: ['./simple-board.component.css']
})
export class SimpleBoardComponent implements OnInit {
  private database : DaoService;
  private service : SessionService;
  private showModal = 'hideModal';
  private list : Observable<any[]>;
  private visibleList = new Array();
  private savedList = new Array();
  private AUTH : string;


  //폼그룹 설정
  private modalData = new FormGroup({
    title: new FormControl('',
      [Validators.required, Validators.minLength(1)]
    ),
    contents: new FormControl('',
      [Validators.required, Validators.minLength(4)]
    )
  });
  
  private lastSize = 5;

  constructor(dao : DaoService, serv : SessionService, private session : SessionService) { 
    this.database = dao;
    this.service = serv;
    console.log(session.getInfor());
    this.AUTH = session.getInfor().auth;
  }

  //초기화 이후 데이터리스트 가져오기
  ngOnInit() {
     this.getDataFromFireBase();
  }

  getDataFromFireBase(){
    if(this.list != undefined && this.list != null){
      this.list.subscribe().closed;
    }
    this.list = this.database.getItemPaging('dash', 'zeroNum', 0, '==', this.lastSize)
      .pipe(
        filter((val) => {
          val.forEach((res) => {
            this.visibleList[res.idx] = false;
            this.savedList.push(res);
          });
          return true;
        })
      );
  }

  paging(event){
    this.lastSize = this.lastSize + 5;
    this.getDataFromFireBase();
  }

  //저장박스 열기
  insertPage(){
	  this.showModal = 'showModal';
  }
  
  //onSumbit 형식으로 저장해보기
  onSubmit() {
    if (this.modalData.status == 'VALID') {
      const idx = this.database.getGenerateId();
      this.modalData.value.idx = idx;
      this.modalData.value.zeroNum = 0;
      this.modalData.value.date = new Date();
      var obj = this.database.setItem('dash', this.modalData.value);
      this.database.getItem('dash', ['idx'], [idx], ['==']).forEach((val) => {
        if (val.length > 0) {
          alert('Complete');
          this.showModal = 'hideModal';
          this.cleanData();
        } else {
          alert('Fail');
        }
      });
    }
  }

  //저장박스 닫기
  closeModal(event){
    event.stopPropagation();
    this.showModal = 'hideModal';
    this.cleanData();
  }
  
  //등록 후 폼 영역 초기화
  cleanData(){
    this.modalData.controls.title.reset();
    this.modalData.controls.contents.reset();  
  }

  //변경하기
  alterData(event,idx,items){
    
    event.stopPropagation();

    if(!confirm('Do you want change the Data? ')){
      return;
    }
      
    this.savedList.forEach((val) => {
        if(val.idx == idx){
          var data = {idx:idx,title:items.title,contents: items.contents,date:new Date()};
          if(items.contents == null) delete data['contents'];
          this.database.alterItem('dash', 'idx', idx, '==', data ,'update');
          return;
        }        
    });
  }

  //제거하기
  removeData(event, idx){
    console.log(idx);
    event.stopPropagation();
    if(!confirm('Do you want Delete the Data? ')){
      return;
    }    
    this.database.alterItem('dash', 'idx', idx, '==', {idx:idx},null);
  }

  //리스트에서 세부보기
  showTextArea(obj,idx){
    obj.stopPropagation();
     if(this.visibleList[idx]){
       this.visibleList[idx] = false;
       obj.srcElement.defaultValue = '-';
     } else {
       this.visibleList[idx] = true;
       obj.srcElement.defaultValue = 'O';
     }
  }
}
