import { Component, OnInit } from '@angular/core';
import { SessionService } from '../service/session.service';
import { Info } from '../service/app.info';
import { fadeAnimation } from '../service/myFadeIn';

//fadeAnimation
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
  animations:[fadeAnimation]
})
export class MainComponent implements OnInit {
  private activeType = 'first';

  private activeArray = [
     [1,true],
     [2,false],
     [3,false],
     [4,false],
     [5,false],
     [6,false],
  ];

  constructor(private session : SessionService) { 

  }

  ngOnInit() {

  }

  getTypeForPaging(event){
    const AUTH = this.session.getInfor().auth;
    if(AUTH != 'SUPER' && event == 6){
      alert('Can not access to User Control Page');
      return;
    }
    this.activeArray.map((val) => {
      if (event == val[0]) {
        val[1] = true;
      } else {
        val[1] = false;
      }
      return val;
    });
    if(event == 1 || event == 2){
      this.activeType = 'first';
    } else if(event == 3 || event == 4){
      this.activeType = 'second';
    } else if(event == 5 ){
      this.activeType = 'third';
    } else{
      this.activeType = 'fourth';
    }

  }

}
